.column{
    color: white;
    padding: 0.5em;
}

label{
    text-align: left;
    margin-right: 1em;
}

input,textarea{
    color:#0040FF;
    width: 100%;
    padding: 10px 10px;
    margin: 8px 0;
    border: 2px solid #0040FF;
    box-sizing: border-box;
    resize: none !important;
    border-radius: 7px;
}

input:focus,textarea:focus{
    outline: none;
}

.row{
    justify-content: center;
    width: 100%;
    margin:0;
}

::placeholder{
    color:#0040FF ;
}

.error::placeholder,.error{
    color: #fa3e3e;
}

.error{
    border: 2px solid #fa3e3e;
}

.save-btn{
    text-align: center;
}

.save-btn>button{
    width: 40%;
}